.menu-phone {
    display: none;
    width: 100%;
    height: auto;
    justify-content: center;
    position: absolute;
    top: 0;
    z-index: 3;
}

.navMob button {
    background: none;
}

.menu-phone .navCategory {
    background: #F4A876;
}

.menu-phone a {
    padding: 15px 10px;
}

nav a:hover {
    //font-size: 16.5px;
    border-bottom: 2px solid;
}

.menu-open-btn {
    margin: 20px 10px;
    position: relative;
    /*display: flex;*/
    width: 30px;
    height: 30px;
    font-size: 24px;
    z-index: 4;
}

.menu-btn-line,
.menu-btn-line::before,
.menu-btn-line::after {
    position: absolute;
    left: 0;
    width: 30px;
    height: 1px;
    background-color: #5C5C5C;
}

.menu-btn-line {
    width: 20px;
    height: 1px;
}

.menu-btn-line::after {
    width: 26px;
}

.menu-btn-line-active::after {
    width: 30px;
}

.menu-btn-line::before,
.menu-btn-line::after {
    content: '';
    display: block;
    transition: 0.5s;
}

.menu-btn-line::before {
    transform: translateY(-5px);
}

.menu-btn-line::after {
    transform: translateY(5px);
}

.menu-btn-line-active {
    height: 0;
}

.menu-btn-line-active::before {
    transform: rotate(45deg);
}

.menu-btn-line-active::after {
    transform: rotate(-45deg);
}

/*---------------------- @media 1360 --------------------------*/
@media screen and (min-width:1360px) {
    .menu-phone {
        position: relative;
        display: flex;
        flex-flow: row wrap;
    }

    .menu-open-btn {
        display: none;
    }

    .menu-phone .navCategory {
        width: 98.9vw;
        position: absolute;
        //bottom: 0;
        bottom: -150px;
        height: 55px;
    }

    .navCategory .listCategory {
        display: flex;
        height: 55px;
    }

    .menu-phone .navCategory {
        display: none;
    }

    .wrapper .navCategoryDestop {
        display: block;
    }
}