* {
    padding: 0;
    margin: 0;
    border: none;
    outline: none;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

header,
main,
footer {
    width: 100%;
    height: auto;
    position: relative;
}

.gridContainer {
    min-width: 320px;
    max-width: 1400px;
    margin: 0 auto;
}

h1 {
    font: normal 3.8rem 'AvenirNextCyr';
    color: #F4A876;
    line-height: 57px;
    text-transform: uppercase;
}

h2 {
    font: normal 3.2rem 'AvenirNextCyr';
    color: #F4A876;
    line-height: 44px;
    text-align: center;
    letter-spacing: 0.05em;
}

h3 {
    font: normal 1.8rem 'AvenirNextCyr';
    line-height: 25px;
    color: #5C5C5C;
}

h4 {
    font: normal 1.6rem 'AvenirNextCyr';
    line-height: 22px;
    color: #787878;
}

p {
    font: normal 1.6rem 'AvenirNextCyr';
    line-height: 24px;
    text-align: center;
    color: #000000;
}

a {
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
}

button {
    cursor: pointer;
}

.wrapper img {
    width: 100%;
}

.colorOrange {
    color: #F4A876;
}

/*--------------------------- header ---------------------------*/
.wrapper .header {
    width: 100%;
    height: 100px;
}

.header .flex {
    height: 100%;
}

.flex .navMob {
    display: flex;
    justify-content: space-between;
    align-items: end;
    position: relative;
}

.navMob .blockLogo {
    margin: 20px;
    position: absolute;
    //width: 206px;
    //height: 60px;
    left: calc(50% - 123px);
}

.navMob .blockBasket {
    margin: 20px;
    position: absolute;
    right: 0;
}

.basket .basketMoney {
    display: none;
}

.basket .basketMeter {
    font: normal 1.4rem AvenirNextCyr;
    line-height: 19px;
    text-align: center;
    color: #f4a876;
    position: absolute;
    top: -14px;
    left: 14px;
}

nav a {
    font: normal 400 1.6rem 'AvenirNextCyr';
    line-height: 22px;
    color: #FFFFFF;
}

.menu-phone .one {
