/*----------------- instagram ------------------*/
.mainContent .instagram {
    margin-top: 130px;
}

.instagram .flex6 {
    border-top: .1rem solid #F4A876;
}

.flex6 h2 {
    width: 310px;
    position: relative;
    top: -22px;
    margin: 0 auto;
    background: #ffffff;
}

.flex6 .linkInsta {
    font: normal 1.6rem 'AvenirNextCyr';
    line-height: 22px;
    letter-spacing: 0.05em;
    color: #C4C4C4;
    position: relative;
    top: -20px;
    left: calc(50% - 68.5px);
}

.flex6 .fotoInst {
    overflow: hidden;
    overflow-x: scroll;
    white-space: nowrap;
    padding-bottom: 30px;
}

.fotoInst::-webkit-scrollbar {
    width: 22px;
    /* 1 - вертикальный скроллбар */
}

.fotoInst::-webkit-scrollbar:horizontal {
    height: 6px;
    /* 1 - горизонтальный скроллбар */
}

.fotoInst::-webkit-scrollbar-track {
    background: rgba(244, 168, 118, 0.25);
    border-top: 3px solid #fff;
    border-bottom: 3px solid #fff;
    /* 3 – трек */
}

.fotoInst::-webkit-scrollbar-track-piece {
    background: rgba(244, 168, 118, 0.25);
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    /* 4 – видимая часть трека */
}

::-webkit-scrollbar-thumb {
    background: #F4A876;
    border-radius: 10px;
    /* 5 - ползунок */
}

.fotoInst .foto {
    position: relative;
    width: 120px;
    vertical-align: top;
    height: 125px;
}

.fotoInst .foto::after {
    content: '';
    display: inline-block;
    position: absolute;
    z-index: 2;
    right: 0;
    left: 0;
    bottom: 300px;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(244, 168, 118, 0.7), rgba(244, 168, 118, 0.7));
}

.foto:hover.foto::after {
    bottom: 0;
    transition: .3s ease-in-out;
}

.foto .goOver {
    width: 100%;
    display: block;
    text-align: center;
    position: absolute;
    bottom: -55px;
    z-index: 3;
    font: normal 1.8rem 'AvenirNextCyr';
    color: #ffffff;
    line-height: 25px;
    letter-spacing: 0.05em;
    transition: .7s ease-in-out;
}

.foto .goOver::before {
    content: '';
    display: block;
    height: 32px;
    background: url('../img/inst_hover.webp') center no-repeat;
    //background: linear-gradient(0deg, rgba(244, 168, 118, 0.7), rgba(244, 168, 118, 0.7));
}

.foto:hover .goOver {
    bottom: calc(50% - 27.5px);
    //z-index: 1;
}

